/* HomePage.css */

body, html {
    margin: 0;
    padding: 0;
    height: 100%;
    font-family: 'Arial', sans-serif;
    background-color: #f4f4f4;
    color: #333;
  }
  
  .homepage {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
  }
  
  .header {
    background-color: #004d40;
    padding: 15px 20px;
    color: white;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 3px solid #333;
    flex-wrap: wrap;
  }
  
  .navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;
  }
  
  .logo {
    font-size: 2rem;
    font-weight: bold;
  }
  
  .nav-links {
    list-style: none;
    display: flex;
    gap: 15px;
  }
  
  .nav-links li {
    display: inline;
  }
  
  .nav-links a {
    text-decoration: none;
    color: white;
    font-size: 1rem;
    padding: 5px 10px;
  }
  
  .nav-links a:hover {
    background-color: #00796b;
    border-radius: 5px;
  }
  
  /* Hero Section Styling */
  
  .hero-section {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 20px;
    padding: 0 20px;
  }
  
  .image-container {
    position: relative;
    overflow: hidden;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    flex: 1;
  }
  
  .hero-image {
    width: 100%;
    height: auto;
    transition: transform 0.3s ease-in-out;
  }
  
  .hero-image:hover {
    transform: scale(1.05);
  }
  
  .image-caption {
    background-color: rgba(0, 0, 0, 0.7);
    color: white;
    padding: 10px;
    position: absolute;
    bottom: 0;
    width: 100%;
    text-align: center;
    font-size: 1.2rem;
    opacity: 0;
    transition: opacity 0.3s ease-in-out;
  }
  
  .image-container:hover .image-caption {
    opacity: 1;
  }
  
  /* Footer Styling */
  
  .footer {
    background-color: #004d40;
    color: white;
    padding: 20px 0;
    margin-top: auto;
  }
  
  .footer-content {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 20px;
  }
  
  .footer-section {
    flex: 1;
    margin-bottom: 20px;
  }
  
  .footer-section h2 {
    font-size: 1.4rem;
    margin-bottom: 10px;
  }
  
  .footer-section p, .footer-section ul {
    font-size: 1rem;
  }
  
  .footer-section ul {
    list-style: none;
    padding: 0;
  }
  
  .footer-section ul li {
    margin-bottom: 5px;
  }
  
  .footer-section ul li a {
    text-decoration: none;
    color: white;
    font-size: 1rem;
  }
  
  .footer-section ul li a:hover {
    text-decoration: underline;
  }
  
  .footer-bottom {
    text-align: center;
    padding: 10px;
    background-color: #00332e;
    border-top: 1px solid #333;
  }
  
  /* Responsive Design */
  
  @media (min-width: 768px) {
    .hero-section {
      flex-direction: row;
      flex-wrap: wrap;
    }
  
    .image-container {
      flex: 1 1 calc(33.333% - 20px);
    }
  
    .footer-content {
      justify-content: space-between;
    }
  }
  
  @media (max-width: 767px) {
    .logo {
      font-size: 1.5rem;
    }
  
    .nav-links a {
      font-size: 0.9rem;
    }
  
    .footer-section {
      flex: 1 1 100%;
    }
  
    .footer-bottom {
      font-size: 0.9rem;
    }
  }
  